.container {
  box-shadow: none !important;
  max-width: none !important;

  th,
  td {
    padding: 0.75rem 1rem;
    text-align: left;

    border: 1px solid #000;
    background-color: #fff;
  }
  th {
    background-color: #fff;
    font-weight: 600;
    color: #1f2937;
    position: sticky;
    top: 0;
    z-index: 10;

    font-size: 0.9rem;
  }
  tr:hover {
    background-color: #f5f5f5;
  }
}

.table-container {
  overflow-x: auto; /* àª•à«‹àª·à«àªŸàª•àª¨à«‡ àª°àª¿àª¸à«àªªà«‹àª¨à«àª¸àª¿àªµ àª¬àª¨àª¾àªµàªµàª¾ àª®àª¾àªŸà«‡ */
}
table {
  width: 100%;
  border-collapse: collapse;
}
th,
td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid #e5e7eb;
}
th {
  background-color: #fff;
  font-weight: 600;
  color: #4b5563;
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.05em;
}
tbody tr:nth-child(even) {
